@import './variables.scss';

html {
    font: $baseFontSize $primaryFont;
    line-height: 1.5;

    @media screen and (max-width: 425px) {
        font-size: $mobileFontSize
    }
}

a {
    color: $primary;
}

form,
input,
button,
textarea {
    font-family: $primaryFont;
    line-height: 1.5;
}

@each $header,
$size in (h1: 2.5em, h2: 2.1em, h3: 1.7em, h4: 1.45em, h5: 1.2em, h6: 1em) {
    #{$header} {
        font-size: $size;
        font-weight: normal;
    }
}

.page-title {
    text-align: center;
    margin-bottom: 1em;
}